.App {
  text-align: center;
  min-height: 100vh;
  background-color: #282c34;
  color: white;
  padding: 20px;
}

.App-header {
  background: #2c3e50;
  padding: 24px;
  color: white;
  text-align: center;
  margin-bottom: 40px;
}

.App-header h1 {
  margin: 0 0 24px;
  font-size: 36px;
}

.search-container {
  position: relative;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  margin: 20px 0;
}

.search-container input {
  padding: 10px 15px;
  font-size: 16px;
  border: 2px solid #4a4a4a;
  border-radius: 8px;
  background-color: #1a1a1a;
  color: white;
  min-width: 250px;
}

.search-container input:focus {
  outline: none;
  border-color: #3498db;
}

.search-container button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.search-container button:hover {
  background-color: #2980b9;
}

.suggestions-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background-color: #1a1a1a;
  border: 2px solid #4a4a4a;
  border-radius: 8px;
  margin-top: 5px;
  max-height: 200px;
  overflow-y: auto;
  z-index: 1000;
}

.suggestion-item {
  padding: 10px 15px;
  cursor: pointer;
  text-align: left;
  transition: background-color 0.2s;
}

.suggestion-item:hover {
  background-color: #2a2a2a;
}

.App-main {
  padding: 0 24px 48px;
}

.error-message {
  color: #e74c3c;
  background-color: rgba(231, 76, 60, 0.1);
  padding: 10px 20px;
  border-radius: 8px;
  margin: 20px auto;
  max-width: 600px;
}
